import MailchimpSubscribe from 'react-mailchimp-subscribe'

const action = 'https://queuedlab.us20.list-manage.com/subscribe/post?u=1ad46e8d8503e34118a3b3b2d&amp;id=af66b5bc1d'

const Form = (onSubmitted, status, message) => {
  const [email, setEmail] = useState('')
  const [steamId, setSteamId] = useState('')

  return (
    <>
      <label for="mce-EMAIL">
        Email Address (Required)
        <input
          type="email" value={email} name="EMAIL" class="required email" id="mce-EMAIL" />
      </label>
      <label for="mce-STEAM_ID">
        Steam ID
        <input type="text" value="" name="STEAM_ID" class="" id="mce-STEAM_ID" />
      </label>
      <div className="status">
        {status === "sending" && <div style={{ color: "blue" }}>Sending...</div>}
        {status === "error" && <div style={{ color: "red" }} dangerouslySetInnerHTML={{__html: message}}/>}
        {status === "success" && <div style={{ color: "green" }}>Subscribed!</div>}
      </div>
    </>
  )
}

export default () => (
  <MailchimpSubscribe
    url={action}
    render={({ subscribe, status, message }) => (
      <Form
        onSubmitted={formData => subscribe(formData)}
        status={status}
        message={message}
      />
    )}
  />
)
